  init = function() {
    sfApp.setup();
    window.onresize(function() {
      'use strict';
      if (this.resizeTO) {
        clearTimeout(this.resizeTO);
      }
      this.resizeTO = setTimeout((function() {
        document.dispatchEvent('resizeEnd');
      }), 500);
    });
    return util.addEvent(window, 'resizeEnd', function(event) {
      'use strict';
      sfApp.refreshIntro();
    });
  };

  module.exports = {
    init: init
  };

